@font-face {
	font-family: 'Proxima Nova Regular';
	src: url('../fonts/ProximaNova-Regular.eot');
	src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
		url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ProximaNova-Regular.woff') format('woff'),
		url('../fonts/ProximaNova-Regular.ttf') format('truetype');
}

@font-face {
	font-family: 'Proxima Nova Bold';
	src: url('../fonts/ProximaNova-Bold.eot');
	src: local('Proxima Nova Bold'), local('ProximaNova-Bold'),
		url('../fonts/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ProximaNova-Bold.woff') format('woff'),
		url('../fonts/ProximaNova-Bold.ttf') format('truetype');
}

@font-face {
	font-family: 'Proxima Nova Bold';
	src: url('../fonts/ProximaNova-BoldIt.eot');
	src: local('Proxima Nova BoldIt'), local('ProximaNova-BoldIt'),
		url('../fonts/ProximaNova-BoldIt.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ProximaNova-BoldIt.woff') format('woff'),
		url('../fonts/ProximaNova-BoldIt.ttf') format('truetype');
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Proxima Nova SemiBold';
	src: url('../fonts/ProximaNova-Semibold.eot');
	src: local('Proxima Nova Semibold'), local('ProximaNova-Semibold'),
		url('../fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ProximaNova-Semibold.woff') format('woff'),
		url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
		font-display: swap;
}

@font-face {
	font-family: 'Proxima Nova Regular';
	src: url('../fonts/ProximaNova-RegularIt.eot');
	src: local('Proxima Nova RegularIt'), local('ProximaNova-RegularIt'),
		url('../fonts/ProximaNova-RegularIt.eot?#iefix') format('embedded-opentype'),
		url('../fonts/ProximaNova-RegularIt.woff') format('woff'),
		url('../fonts/ProximaNova-RegularIt.ttf') format('truetype');
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Berlingske Serif Regular';
	src: url('../fonts/BerlingskeSerif-Regular.ttf');
	font-display: swap;
}

@font-face {
	font-family: 'Berlingske Serif SemiBold';
	src: url('../fonts/BerlingskeSerif-DBd.ttf');
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Berlingske Serif SemiBold';
	src: url('../fonts/BerlingskeSerif-DBdItalic.ttf');
	font-style: italic;
	font-display: swap;
}