.modal {
    width: 630px;
    max-width: calc(100% - 40px)!important;
    max-height: calc(100% - 40px)!important;
    .iziModal-content {
        padding: 70px 100px 35px !important; }
    .section-title {
        text-align: left;
        font-size: 36px;
        margin-bottom: 75px; }
    .section-post-title {
        font-size: 16px;
        margin: 0; }
    input {
        width: 100%;
        max-height: 70px;
        text-align: center;
        transition: border-color .3s; }
    input[type="submit"] {
        padding: 35px; } }
.datepicker {
    width: 100%; }
.datepicker__prev,
.datepicker__next {
        color: #F84388 !important; }


.datepicker__wrapper {
    left: 50% !important;
    transform: translateX(-50%); }

#stock-modal .section-title {
    font-size: 32px; }

#video-modal {
    width: 900px;
    .iziModal-content {
        padding: 0!important; }
    .section-title {
        margin-bottom: 30px; } }

.video-container {
    padding-bottom: 56.25%;
    width: 100%;
    height: 0;
    overflow: hidden;
    position: relative;
    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; } }


.modal .form-line,
input[type="submit"] {
    margin-bottom: 20px; }

#feedback-modal input[type="submit"] {
    margin-top: 20px; }
