#capture-form {
    padding-top: 85px;
    padding-bottom: 20px;
    background: #FCFCFC;
    overflow: hidden; }

.capture-container {
    position: relative;
    display: flex;
    justify-content: flex-end; }

.capture-img {
    position: absolute;
    top: 50%;
    left: -85px;
    transform: translateY(-58%); }

.capture-txt {
    line-height: 122.02%;
    color: #747474;
    margin: 30px 0 60px; }

.capture-form {
    display: flex;
    justify-content: space-between;
    input {
        width: 270px; }
    .fill-btn {
        margin-bottom: 0; }
    .form-line {
        display: inline;
        margin-right: 25px; } }

.capture-desc {
    font-size: 12px;
    line-height: 122.02%;
    color: #747474;
    display: flex;
    margin-top: 30px;
    margin-bottom: 5px;
    p {
        margin-top: 7px;
        margin-left: 10px; } }

.capture-content {
    z-index: 6; }
